@import url(partials/reset.css);

@import url(partials/fonts.css);

@import url(partials/swiper.css);

@import url(partials/jquery.fancybox.css);

/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
}

body {
  color: #ffffff;
  font-family: "Futura PT", sans-serif;
  margin: 0;
  font-size: 1vw;
  background: #000012;
  width: 100%;
  overflow-x: hidden;
}

.container {
  margin: 0 0 0 14.27083vw;
}

.wrapper {
  overflow: hidden;
  width: 100%;
}

.text-style {
  color: #ffffff;
  font-weight: 400;
  line-height: 1.875vw;
  font-size: 1.5625vw;
}

.text-style p + p {
  margin-top: 1em;
}

.text-style p span,
.text-style p a {
  color: #ffffff;
  background-color: #e41529;
}

.block-title {
  color: #ffffff;
  font-size: 3.4375vw;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 0.8;
}

.block-subtitle {
  color: #ffffff;
  font-size: 2.1875vw;
  font-weight: 700;
}

.marquee {
  color: #ffffff;
  font-size: 1.09375vw;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 0.8;
  display: block;
  position: relative;
  user-select: none;
}

.js-marquee {
  float: none !important;
  padding: 0.72917vw 0;
  background: #e41529;
}

.js-marquee-wrapper {
  display: flex;
}

.woman {
  width: 8.69792vw;
  display: block;
  position: fixed;
  left: 2.23958vw;
  top: 2.29167vw;
  z-index: 10;
}

.woman img {
  display: block;
  width: 100%;
}

.kiabi {
  width: 6.875vw;
  display: block;
  position: fixed;
  right: 2.86458vw;
  top: 2.29167vw;
  z-index: 10;
}

.kiabi img {
  display: block;
  width: 100%;
}

.section-1 {
  position: relative;
}

.section-1 .container {
  display: flex;
  align-items: flex-start;
}

.section-1 .container .left {
  margin-top: 6.77083vw;
}

.section-1 .container .left .title {
  width: 37.70833vw;
}

.section-1 .container .left .title span {
  color: #ffffff;
  font-size: 7.5vw;
  font-weight: 700;
  letter-spacing: 0.08281vw;
  line-height: 0.8;
  display: block;
}

.section-1 .container .left .title span:first-child {
  text-align: left;
  position: relative;
}

.section-1 .container .left .title span:first-child::before {
  content: "";
  display: block;
  width: 6.19792vw;
  height: 10.46875vw;
  position: absolute;
  left: 8.85417vw;
  top: -3.64583vw;
  background: url("../img/stx.png") center top no-repeat;
  background-size: contain;
}

.section-1 .container .left .title span:last-child {
  text-align: right;
}

.section-1 .container .left .text {
  margin-top: 1.82292vw;
  width: 33.33333vw;
  font-size: 1.35417vw;
  line-height: 1.6875vw;
}

.section-1 .container .right {
  width: 33.4375vw;
  margin-left: 3.07292vw;
}

.section-1 .container .right .img {
  display: block;
  width: 100%;
}

.section-1 .lines {
  position: absolute;
  bottom: 3.38542vw;
  left: 0;
  width: 100%;
}

.section-1 .marquee-1 {
  transform: rotate(3.5deg);
  z-index: 1;
}

.section-1 .marquee-2 {
  transform: rotate(-5deg);
  z-index: 2;
}

.section-2 {
  padding-top: 5.72917vw;
  padding-bottom: 5.72917vw;
}

.section-2 .step {
  width: 69.94792vw;
}

.section-2 .step.step-1 .block-title {
  margin-bottom: 2.08333vw;
}

.section-2 .step.step-1 .how-flex {
  display: flex;
}

.section-2 .step.step-1 .how-flex .how .img {
  display: block;
  width: 2.29167vw;
  height: 2.29167vw;
}

.section-2 .step.step-1 .how-flex .how .img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.section-2 .step.step-1 .how-flex .how .big {
  margin-top: 1.04167vw;
  color: #ffffff;
  font-size: 1.97917vw;
  font-weight: 700;
  text-transform: uppercase;
}

.section-2 .step.step-1 .how-flex .how .small {
  color: #ffffff;
  font-size: 1.35417vw;
  line-height: 1.66667vw;
}

.section-2 .step.step-1 .how-flex .how + .how {
  margin-left: 5vw;
  position: relative;
}

.section-2 .step.step-1 .how-flex .how + .how::before {
  content: "";
  display: block;
  width: 0.83333vw;
  height: 1.45833vw;
  background: url("../img/arr-h.png") center center no-repeat;
  background-size: contain;
  position: absolute;
  left: -2.60417vw;
  top: 3.75vw;
}

.section-2 .step.step-1 .box {
  margin-top: 1.82292vw;
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 2.1875vw;
  overflow: hidden;
}

.section-2 .step.step-1 .box .marquee {
  position: absolute;
  width: 100%;
}

.section-2 .step.step-1 .box .marquee-1 {
  left: 2.1875vw;
  top: 0;
  z-index: 1;
}

.section-2 .step.step-1 .box .marquee-2 {
  transform: rotate(90deg);
  transform-origin: right  top;
  left: 0;
  bottom: 0;
  z-index: 2;
}

.section-2 .step.step-1 .box .marquee-3 {
  transform: rotate(180deg);
  right: 2.1875vw;
  bottom: 0;
  z-index: 3;
}

.section-2 .step.step-1 .box .marquee-4 {
  transform: rotate(-90deg);
  transform-origin: left  top;
  left: 0;
  bottom: 0;
  z-index: 3;
}

.section-2 .step.step-1 .box .flex-block {
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  padding: 5.20833vw 1.04167vw;
}

.section-2 .step.step-1 .box .flex-block .item {
  text-align: center;
  line-height: 32px;
}

.section-2 .step.step-1 .box .flex-block .item .label {
  margin-top: 1.04167vw;
  color: #ffffff;
  font-size: 1.97917vw;
  font-weight: 700;
  text-transform: uppercase;
}

.section-2 .step.step-1 .box .flex-block .item .text {
  color: #ffffff;
  font-size: 1.35417vw;
  line-height: 1.66667vw;
}

.section-2 .step.step-1 .btn-light {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #e41529;
  font-size: 1.66667vw;
  line-height: 1;
  font-weight: 700;
  text-transform: uppercase;
  width: 15.9375vw;
  height: 4.27083vw;
  background-color: #ffffff;
  margin: 2.29167vw auto 0;
  transition: all 0.2s ease;
}

.section-2 .step.step-1 .btn-light:hover {
  background-color: #e41529;
  color: #ffffff;
}

.section-3 {
  position: relative;
  padding-top: 5.20833vw;
}

.section-3 .block-title {
  position: relative;
  margin-bottom: 2.86458vw;
}

.section-3 .block-title::before {
  content: "";
  display: block;
  width: 3.4375vw;
  height: 5.88542vw;
  position: absolute;
  left: 3.64583vw;
  top: -3.38542vw;
  background: url("../img/stx.png") center top no-repeat;
  background-size: contain;
}

.section-3 .text-1 {
  width: 48.17708vw;
}

.section-3 .block-subtitle {
  width: 51.04167vw;
  margin-top: 1.5625vw;
  margin-bottom: 1.5625vw;
}

.section-3 .flex-block {
  display: flex;
  align-items: flex-start;
}

.section-3 .flex-block .left {
  position: relative;
  z-index: 4;
  width: 30.46875vw;
}

.section-3 .flex-block .left .img {
  margin-top: 11.45833vw;
  width: 30vw;
}

.section-3 .flex-block .left .img img {
  display: block;
  width: 100%;
}

.section-3 .flex-block .right {
  width: 32.5vw;
  margin-top: 3.28125vw;
  margin-left: 5.05208vw;
  position: relative;
  z-index: 2;
}

.section-3 .flex-block .right .img {
  width: 100%;
}

.section-3 .flex-block .right .img img {
  width: 100%;
  display: block;
}

.section-3 .lines {
  position: absolute;
  left: 0;
  width: 100%;
  bottom: 18.22917vw;
}

.section-3 .marquee {
  font-size: 20px;
}

.section-3 .marquee .js-marquee {
  padding: 1.04167vw 0 0.41667vw;
}

.section-3 .marquee span {
  position: relative;
}

.section-3 .marquee span::before {
  content: "";
  display: block;
  width: 1.19792vw;
  height: 1.875vw;
  position: absolute;
  left: 1.04167vw;
  top: -0.78125vw;
  background: url("../img/stxb.png") center top no-repeat;
  background-size: contain;
}

.section-3 .marquee-1 {
  transform: rotate(3.5deg);
  z-index: 1;
}

.section-3 .marquee-2 {
  transform: rotate(-6deg);
  z-index: 3;
}

.section-4 {
  padding-top: 5.72917vw;
}

.section-4 .block-title {
  margin-bottom: 2.86458vw;
}

.section-4 .text-1 {
  width: 47.13542vw;
}

.section-4 .swiper-container {
  width: 100%;
  box-sizing: border-box;
  padding-left: 10.41667vw;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide {
  margin-right: 5.20833vw;
  margin-left: 3.85417vw;
  transition: 0.2s ease;
  width: auto;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex {
  display: flex;
  align-items: center;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .left {
  width: 32.5vw;
  height: 48.75vw;
  transition: 0.2s ease;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .left img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right {
  padding-left: 3.02083vw;
  width: 15.625vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 48.75vw;
  color: #ffffff;
  font-weight: 400;
  line-height: 1.875vw;
  font-size: 1.5625vw;
  transition: 0.2s ease;
  overflow: hidden;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .title {
  font-weight: 700;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .text {
  margin-top: 1em;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .text p + p {
  margin-top: 1em;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .text p span,
.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .text p a {
  color: #ffffff;
  background-color: #e41529;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .text.long {
  font-size: 1.35417vw;
  line-height: 1.5625vw;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .control {
  margin-top: 1.5625vw;
  display: flex;
  justify-content: space-between;
  width: 8.69792vw;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .control > div {
  cursor: pointer;
  width: 3.17708vw;
  height: 3.17708vw;
  display: block;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: transform 0.2s ease;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .control > div:hover {
  transform: scale(1.1);
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .control > div.button-next {
  background-image: url("../img/arr-r.png");
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide .inner-flex .right .control > div.button-prev {
  background-image: url("../img/arr-l.png");
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide:not(.swiper-slide-active) {
  margin-right: 0;
  margin-left: 2.08333vw;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide:not(.swiper-slide-active) .inner-flex .left {
  width: 18.48958vw;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide:not(.swiper-slide-active) .inner-flex .right {
  width: 0;
  padding-left: 0;
  transform: scale(0);
  opacity: 0;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide:first-child .inner-flex .right .control {
  justify-content: flex-end;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide:first-child .inner-flex .right .control > div.button-prev {
  display: none;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide:last-child {
  margin-right: 68.75vw !important;
}

.section-4 .swiper-container .swiper-wrapper .swiper-slide:last-child .inner-flex .right .control > div.button-next {
  display: none;
}

.section-5 {
  padding-top: 2.86458vw;
}

.section-5 .text {
  width: 51.30208vw;
}

.section-5 .flex-block {
  margin-top: 3.125vw;
