}

.section-5 .flex-block .right .img {
  width: 100%;
}

.section-5 .flex-block .right .img img {
  width: 100%;
  display: block;
}

.to-top-block {
  margin-top: 3.64583vw;
  text-align: center;
}

.to-top-block .to-top {
  display: inline-block;
}

.to-top-block .to-top img {
  display: inline-block;
  vertical-align: middle;
  width: 3.17708vw;
  transition: transform 0.2s ease;
}

.to-top-block .to-top span {
  display: inline-block;
  vertical-align: middle;
  margin-left: 0.78125vw;
  color: #ffffff;
  font-size: 30px;
}

.to-top-block .to-top:hover img {
  transform: scale(1.1);
}

.copy {
  padding: 4.16667vw 0 0.52083vw;
}

.copy .text {
  text-align: center;
  color: #ffffff;
  font-size: 1.09375vw;
}